
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'scanning',
data(){
return {
vuegConfig:{
disable: false
}
}
},
created(){

},
methods:{
onerror(err){
mui.alert("识别出错");
},
onmarked( type, result ) {
let text = '未知: ';











oScan.vibrate = true;
alert(result)

oScan.start();


},
startRecognize() {
const _this = this;
oScan = new plus.barcode.Barcode('bcid');
setTimeout(function(){
oScan.onmarked = _this.onmarked;
oScan.onerror = _this.onerror;
oScan.start();
},1000);
}
},
mounted(){
setTimeout(()=>{
plus&&this.startRecognize();
}, 400);
},
beforeRouteLeave (to, from, next) {
next();
oScan.close();
},
watch:{

},
components:{
XHeader
}
}



